import { Dialog, DialogContent, DialogActions, DialogContentText, DialogTitle } from '@mui/material'
import React, { useContext } from 'react'
import { Context } from '../context/context'

export const Ohje : React.FC = () : React.ReactElement => {

    const { avaus, setAvaus } = useContext(Context)

  return (
    <Dialog open={avaus.avaa} onClose={() => setAvaus({avaa : false, ohjeNro : 0})}>
    <DialogTitle>Ohje</DialogTitle>
    <DialogContent>
      {
        (avaus.ohjeNro === 1)
        ?<DialogContentText>
          Duplikaattifiltteri ky lpi sytetyt tiedostot ja palauttaa nkymn kaikki samanlaiset rivit.
          <br/>
          <br/>
          <b>Tuotenumero / VNR</b>  Palauttaa kaikki rivit, joissa on sama tuotenumero.
          <br/>
          <br/>
          <b>Tuotenumero (TARKKA)</b>  Palauttaa kaikki rivit, jossa koko tuotenimirivi on <b>tsmlleen</b> samanlainen. Esimerkiksi, "Burana 600mg 10 tabletti" ja "Burana 600mg 10 tabl" ei tuottaisi tulosta.
          <br/>
          <br/>
          <b>Tuotenumero (Rivin 3 ensimmist sanaa)</b>  Palauttaa kaikki rivit, jossa rivin 2 ensimmist sanaa ovat samanlaiset. Esimerkiksi "Burana 600 mg 100 tabl" ja "Burana 600 mg 50 tabl" tuottaisi tuloksen, koska molemmista lytyvt sanat "Burana 600 mg"
        </DialogContentText>
        :(avaus.ohjeNro === 2)
        ?<DialogContentText>
          Voit hakea mill tahansa sanalla tai kirjainyhdistelmll, jolloin hakutuloksiin palautuu kaikki rivit, josta kyseinen kirjainsarja lytyy.
          <br/>
          <br/>
          Esimerkiksi "ran" haku palauttaisi tuloksiin muun muassa Buranan.
          <br/>
          <br/>
          Jos duplikaattifiltterinti on pll, voit mys rajata tuloksia tll haulla.
        </DialogContentText>
        :<></>
      }
    </DialogContent>
    <DialogActions>
      
    </DialogActions>
    </Dialog>
  )
}
